import { useContext, useEffect, useState } from "react";
import shortid from 'shortid';
import { useCollection, useCollectionData } from 'react-firebase-hooks/firestore';
import { collection, Firestore, getFirestore, orderBy, query, serverTimestamp, updateDoc } from "firebase/firestore";
import {app} from './index'
import { Context } from "../..";
import Loader from "../Loader/Loader";
import { doc, addDoc } from "firebase/firestore"; 
import { getAuth } from "firebase/auth";
import { useAuthState } from "react-firebase-hooks/auth";
import { onSnapshot } from "firebase/firestore";



const Messages = () => {
    const auth = getAuth()
    const [user, isLoadingAuth] = useAuthState(auth)

    const {app} = useContext(Context)
    const firestore = getFirestore()

    const [messages, setMessages] = useState([]);

    useEffect(() => {

        return onSnapshot(query(collection(firestore, 'messages'), orderBy('createdAt', 'asc')), snapshot => {
            setMessages(snapshot.docs)
        })

    }, []);

    console.log(messages.map(el => el.data()));
    // const [messages, isLoading, error] = useCollectionData(
    //     collection(getFirestore(app), 'messages')
    //     , {
    //     snapshotOptions: orderBy('creasdedAt', 'desc')
    // });
    //   .orderBy('createdAt', 'desc') 
        // const [messages, isLoading, error] = useCollectionData(
        // collection( getFirestore(app), 'messages'));
    // const unsub = onSnapshot(doc(firestore), 'messages', (doc) => {
    //     console.log(doc.data());
    // })
    
    if (messages.length == 0) {
        return <Loader />
    }
    return <>
        <ul className="messages__list">
            {messages && messages.map((el, i) => {
                // console.log(el.data().userId == user.uid);
                return <li className={el.data().userId == user.uid? 'message__item-left' : 'message__item-right'} key={el.data().createdAt}>
                    <h3>{el.data().userName}</h3>
                    <p>{el.data().message}</p>
                </li>
            })}
            {/* {data && data.map(el => {

                const options = {
                hour: 'numeric',
                minute: 'numeric',
                second: 'numeric'
                };

                const date = new Date(el.createdAt).toLocaleDateString("ru", options)
                return <li className="message__item" key={el.data().id}>
                    <div>
                        <h1 className="message__name">{ el.name }</h1> <p className="message__date">{`${date}`}</p>
                    </div>
                    <h2 className="message__message">{el.data().message}</h2>
                    <button type='button' className="message__delete" onClick={() => onDelete(el)}>Удалить</button>  
                </li>
            })} */}
        </ul>
    </>
  
}

export default Messages;