import { useContext, useState } from "react";
import shortid from 'shortid';
import { useCollectionData } from 'react-firebase-hooks/firestore';
import { collection, Firestore, getFirestore, serverTimestamp, updateDoc } from "firebase/firestore";
import {app} from './index'
import { Context } from "../..";
import Loader from "../Loader/Loader";
import { doc, addDoc } from "firebase/firestore"; 
import { getAuth } from "firebase/auth";
import { useAuthState } from "react-firebase-hooks/auth";




const EntryField = () => {
    const auth = getAuth()
    const [user, isLoadingAuth] = useAuthState(auth)

    // console.log(user);
    const firestore = getFirestore()

    // console.log(user.uid);
  const [message, setMessage] = useState('');
    let date = new Date
    // console.log(date.getHours(), date.getMinutes(), date.getSeconds());
    // console.log(Date.now());
    const submitPost = async (e) => {
        e.preventDefault()

        if (message.trim() == '') {
            alert('Введите сообщение ёпт')
            return
        }
        
        // firestore.collection('messages').add({message})
    //     const updateTimestamp = await updateDoc(docRef, {
    // timestamp: serverTimestamp()
    //     });
    //     console.log(updateTimestamp);
        const ref = await addDoc(collection(firestore, 'messages'), {
            userId: user.uid,
            userName: user.displayName,
            message,
            createdAt: Date.now()
        })
    }

    if (isLoadingAuth) {
        return <Loader/>
    }

    return <div className="entry-field">
        <form className="entry-field__form">

            {/* <input className="entry-field__input" placeholder='Имя' onChange={(e) => setName(e.currentTarget.value)}/> */}

            <textarea className="entry-field__input" placeholder='Сообщение' onChange={(e) => setMessage(e.currentTarget.value)} />

            <button className="entry-field__button" type="submit" onClick={submitPost}>Отправить</button>

        </form>
    </div>
}


export default EntryField;