import { NavLink } from "react-router-dom";
import { useAuthState } from 'react-firebase-hooks/auth';
import { getAuth, signOut } from 'firebase/auth';
import { Hearts } from "react-loader-spinner";




const Navbar = () => {
    const auth = getAuth()
    const [user, isLoading, error] = useAuthState(auth)


    return (
        <header>
            <div className='title' style={{display: 'flex'}}>
                <h3 style={{ marginRight: '20px'}}>Чат</h3>
            <Hearts color="#ff6347" height={80} width={80} />
            </div>
            <nav className="header__navigation">
                {user ?
                    <div className="header__out-wrapper">
                        <h3>{user.displayName}</h3>
                        <NavLink className={'header__link'} to={'/login'}>
                            <button onClick={() => signOut(auth).then(res => res)} className={'header__button'}>Выйти</button>
                        </NavLink>
                    </div>
                    :
                    <NavLink className={'header__link'} to={'/chat'}>
                        <button className={'header__button'}>Войти</button>
                    </NavLink>
                }
            </nav>

        </header>
    );
}

export default Navbar;
